.product {
    border: 2px solid gray;
    padding: 5px;
    position: relative;
}
.product-img img {
    max-width: 100%;
}
.product-info {
    margin: 0 0 60px 10px ;
}
.product-name {
    font-weight: 500;
    padding: 10px 0;
}

.cart-btn {
    /* margin-top: 20px; */
    padding: 20px;
    border: none;
    width: 100%;
    background-color: lightblue;
    position: absolute;
    bottom: 0;
    left: 0;
    cursor: pointer;
}
