.container {
    display: grid;
    grid-template-columns: 3fr 1fr;
    padding: 5%;

}

.products {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 15px;
    margin: 0 40px 0 0;
}

.shopping-cart {
    background-color: lightpink;
}


@media only screen and (max-width: 1100px) {
    .products {
        grid-template-columns: repeat(2, 1fr);
        margin: 0 30px 0 0;
    }
}